import React from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, Root, View, CellButton, Search, TabsItem, Tabs, Icon28PictureOutline, Header, Button, Group, Div, PanelHeaderBack } from '@vkontakte/vkui';

import persi from '../img/pers.png'


const Test = ({ id, go, fetchedUser, props})  => ( 
	<Panel id={id}>
		<PanelHeader
			left={<PanelHeaderBack onClick={go} data-to="home" />}
		>
			Kote
		</PanelHeader>
		<img className="Per" src={persi} alt="click" />

		<Button mode="secondary" onClick={go} data-to="home">
			he
		</Button>
	</Panel>
);

Test.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
}; 

export default Test;